class SearchManager {
  constructor() {
    this.searchContainer = document.querySelector('.search-container');
    this.searchButton = document.querySelector('.search-button');
    this.searchInput = document.getElementById('search-input');
    this.searchResults = document.getElementById('search-results');
    this.searchIndex = [];
    this.init();
  }

  init() {
    this.loadSearchIndex();
    this.setupEventListeners();
  }

  loadSearchIndex() {
    try {
      // ä»Žé¡µé¢ä¸­åŠ è½½æœç´¢ç´¢å¼•
      const searchIndexElement = document.getElementById('search-index-data');
      if (searchIndexElement) {
        const indexText = searchIndexElement.textContent.trim();
        // å¤„ç†åŒé‡ç¼–ç çš„JSONå­—ç¬¦ä¸²
        if (indexText.startsWith('"') && indexText.endsWith('"')) {
          const decodedText = JSON.parse(indexText);
          this.searchIndex = JSON.parse(decodedText);
        } else {
          this.searchIndex = JSON.parse(indexText);
        }
        console.log('æœç´¢ç´¢å¼•åŠ è½½æˆåŠŸï¼Œå…±', this.searchIndex.length, 'æ¡è®°å½•');
      } else {
        console.log('æœªæ‰¾åˆ°æœç´¢ç´¢å¼•æ•°æ®');
      }
    } catch (error) {
      console.error('åŠ è½½æœç´¢ç´¢å¼•å¤±è´¥:', error);
    }
  }

  setupEventListeners() {
    // æœç´¢æŒ‰é’®ç‚¹å‡»äº‹ä»¶
    if (this.searchButton) {
      this.searchButton.addEventListener('click', (e) => {
        e.preventDefault();
        this.toggleSearch();
      });
    }

    // æœç´¢è¾“å…¥æ¡†äº‹ä»¶
    if (this.searchInput) {
      this.searchInput.addEventListener('input', Utils.debounce((e) => {
        this.handleSearch(e.target.value, this.searchResults);
      }, 300));
      
      this.searchInput.addEventListener('focus', () => {
        if (this.searchResults) {
          this.searchResults.classList.remove('hidden');
        }
      });
      
      // æœç´¢æ¡†å¤±åŽ»ç„¦ç‚¹æ—¶éšè—æœç´¢ç»“æžœ
      this.searchInput.addEventListener('blur', () => {
        setTimeout(() => {
          if (this.searchResults && !this.searchResults.contains(document.activeElement)) {
            this.searchResults.classList.add('hidden');
          }
        }, 200);
      });
    }
    
    // ç‚¹å‡»å¤–éƒ¨å…³é—­æœç´¢æ¡†å’Œæœç´¢ç»“æžœ
    document.addEventListener('click', (e) => {
      if (this.searchContainer && !this.searchContainer.contains(e.target)) {
        this.closeSearch();
      }
    });
  }

  toggleSearch() {
    if (this.searchContainer) {
      const isOpen = this.searchContainer.classList.contains('show');
      if (isOpen) {
        this.closeSearch();
      } else {
        this.openSearch();
      }
    }
  }

  openSearch() {
    if (this.searchContainer) {
      this.searchContainer.classList.add('show');
      // èšç„¦åˆ°æœç´¢è¾“å…¥æ¡†
      if (this.searchInput) {
        setTimeout(() => {
          this.searchInput.focus();
        }, 100);
      }
    }
  }

  closeSearch() {
    if (this.searchContainer) {
      this.searchContainer.classList.remove('show');
      // æ¸…ç©ºæœç´¢è¾“å…¥æ¡†
      if (this.searchInput) {
        this.searchInput.value = '';
      }
      // éšè—æœç´¢ç»“æžœ
      if (this.searchResults) {
        this.searchResults.classList.add('hidden');
      }
    }
  }

  handleSearch(query, resultsContainer) {
    if (query.length < 2) {
      if (resultsContainer) {
        resultsContainer.classList.add('hidden');
      }
      return;
    }
    
    if (resultsContainer) {
      resultsContainer.classList.remove('hidden');
      this.performSearch(query, resultsContainer);
    }
  }

  performSearch(query, resultsContainer) {
    console.log('æ‰§è¡Œæœç´¢ï¼ŒæŸ¥è¯¢è¯:', query);
    console.log('æœç´¢ç´¢å¼•é•¿åº¦:', this.searchIndex.length);
    
    if (this.searchIndex.length === 0) {
      console.log('æœç´¢ç´¢å¼•ä¸ºç©º');
      this.displayResults([], resultsContainer, query);
      return;
    }

    const results = this.searchIndex.filter(item => {
      const searchText = [
        item.title || '',
        item.description || '',
        item.summary || '',
        ...(item.tags || []),
        ...(item.categories || [])
      ].join(' ').toLowerCase();
      
      const matches = searchText.includes(query.toLowerCase());
      if (matches) {
        console.log('æ‰¾åˆ°åŒ¹é…é¡¹:', item.title);
      }
      return matches;
    }).slice(0, 10); // é™åˆ¶ç»“æžœæ•°é‡

    console.log('æœç´¢ç»“æžœæ•°é‡:', results.length);
    this.displayResults(results, resultsContainer, query);
  }

  displayResults(results, resultsContainer, query) {
    if (!resultsContainer) return;
    
    resultsContainer.innerHTML = '';
    
    if (results.length === 0) {
      resultsContainer.innerHTML = `
        <div class="search-no-results">
          <p>æœªæ‰¾åˆ°ç›¸å…³ç»“æžœ</p>
        </div>
      `;
      return;
    }
    
    results.forEach(item => {
      const highlightedTitle = this.highlightText(item.title || '', query);
      const highlightedSummary = this.highlightText(item.summary || item.description || '', query);
      const tags = (item.tags || []).slice(0, 3);
      
      const resultItem = document.createElement('div');
      resultItem.className = 'search-result-item';
      resultItem.onclick = () => window.location.href = item.url;
      
      resultItem.innerHTML = `
        <div class="search-result-title">${highlightedTitle}</div>
        <div class="search-result-summary">${highlightedSummary}</div>
        <div class="search-result-meta">
          <div class="search-result-tags">
            ${tags.map(tag => `<span class="search-result-tag">${tag}</span>`).join('')}
          </div>
          <span>${this.formatDate(item.date)}</span>
        </div>
      `;
      
      resultsContainer.appendChild(resultItem);
    });
  }

  highlightText(text, query) {
    if (!query) return text;
    
    const regex = new RegExp(`(${query.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')})`, 'gi');
    return text.replace(regex, '<span class="search-highlight">$1</span>');
  }

  formatDate(dateString) {
    if (!dateString) return '';
    
    const date = new Date(dateString);
    const now = new Date();
    const diffTime = Math.abs(now - date);
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    
    if (diffDays === 1) {
      return 'ä»Šå¤©';
    } else if (diffDays <= 7) {
      return `${diffDays}å¤©å‰`;
    } else {
      return date.toLocaleDateString('zh-CN');
    }
  }
}

// è¯­è¨€åˆ‡æ¢åŠŸèƒ½
class LanguageManager {
  constructor() {
    this.languageToggle = document.getElementById('language-toggle');
    this.languageDropdown = document.getElementById('language-dropdown');
    this.languageArrow = document.querySelector('.language-arrow');
    // this.languageOptions = document.querySelectorAll('.language-option');
    this.currentLanguage = document.getElementById('current-language');
    this.init();
  }

  init() {
    this.setupEventListeners();
  }

  setupEventListeners() {
    if (this.languageToggle) {
      this.languageToggle.addEventListener('click', (e) => {
        e.preventDefault();
        this.toggleDropdown();
      });
    }

    // è¯­è¨€é€‰é¡¹ç‚¹å‡»äº‹ä»¶
    // this.languageOptions.forEach(option => {
    //   option.addEventListener('click', (e) => {
    //     e.preventDefault();
    //     const lang = option.getAttribute('data-lang');
    //     const url = option.getAttribute('data-url');
    //     this.switchLanguage(lang, url);
    //   });
    // });

    // ç‚¹å‡»å¤–éƒ¨å…³é—­ä¸‹æ‹‰èœå•
    document.addEventListener('click', (e) => {
      if (this.languageToggle && !this.languageToggle.contains(e.target) && 
          this.languageDropdown && !this.languageDropdown.contains(e.target)) {
        this.closeDropdown();
      }
    });
  }

  toggleDropdown() {
    if (this.languageDropdown) {
      const isOpen = this.languageDropdown.classList.contains('show');
      if (isOpen) {
        this.closeDropdown();
      } else {
        this.openDropdown();
      }
    }
  }

  openDropdown() {
    if (this.languageDropdown) {
      this.languageDropdown.classList.add('show');
    }
    if (this.languageArrow) {
      this.languageArrow.classList.add('rotate');
    }
  }

  closeDropdown() {
    if (this.languageDropdown) {
      this.languageDropdown.classList.remove('show');
    }
    if (this.languageArrow) {
      this.languageArrow.classList.remove('rotate');
    }
  }

  switchLanguage(lang, url) {
    // æ›´æ–°é€‰ä¸­çŠ¶æ€
    // this.languageOptions.forEach(option => {
    //   if (option.getAttribute('data-lang') === lang) {
    //     option.classList.add('selected');
    //   } else {
    //     option.classList.remove('selected');
    //   }
    // });

    // å…³é—­ä¸‹æ‹‰èœå•
    this.closeDropdown();

    // è·³è½¬åˆ°å¯¹åº”è¯­è¨€é¡µé¢
    if (url) {
      // ç¡®ä¿URLæ˜¯å®Œæ•´çš„è·¯å¾„
      const currentPath = window.location.pathname;
      let targetUrl = url;
      
      // å¦‚æžœæ˜¯å½“å‰é¡µé¢ï¼Œç›´æŽ¥è·³è½¬åˆ°å¯¹åº”è¯­è¨€ç‰ˆæœ¬
      if (url === window.location.pathname || url === window.location.pathname + '/') {
        if (lang === 'zh-ch') {
          // åˆ‡æ¢åˆ°ä¸­æ–‡ï¼Œç§»é™¤/enå‰ç¼€
          targetUrl = currentPath.replace(/^\/en/, '');
        } else {
          // åˆ‡æ¢åˆ°è‹±æ–‡ï¼Œæ·»åŠ /enå‰ç¼€
          targetUrl = currentPath.startsWith('/en') ? currentPath : `/en${currentPath}`;
        }
      }
      
      console.log('Switching to language:', lang, 'URL:', targetUrl);
      window.location.href = targetUrl;
    }
  }
}

// æ»šåŠ¨æ•ˆæžœ
class ScrollEffects {
  constructor() {
    this.header = document.getElementById('main-header');
    this.scrollTopBtn = document.getElementById('scroll-top');
    this.init();
  }

  init() {
    this.setupScrollEffects();
  }

  setupScrollEffects() {
    // å¯¼èˆªæ æ»šåŠ¨æ•ˆæžœ
    if (this.header) {
      window.addEventListener('scroll', () => {
        if (window.scrollY > 100) {
          this.header.classList.add('scrolled');
        } else {
          this.header.classList.remove('scrolled');
        }
      });
    }
    
    // æ»šåŠ¨åˆ°é¡¶éƒ¨æŒ‰é’®
    if (this.scrollTopBtn) {
      window.addEventListener('scroll', () => {
        if (window.scrollY > 300) {
          this.scrollTopBtn.classList.add('show');
        } else {
          this.scrollTopBtn.classList.remove('show');
        }
      });
      
      this.scrollTopBtn.addEventListener('click', () => {
        window.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
      });
    }
  }
}

// å·¥å…·å‡½æ•°
const Utils = {
  debounce(func, wait) {
    let timeout;
    return function executedFunction(...args) {
      const later = () => {
        clearTimeout(timeout);
        func(...args);
      };
      clearTimeout(timeout);
      timeout = setTimeout(later, wait);
    };
  },

  throttle(func, limit) {
    let inThrottle;
    return function() {
      const args = arguments;
      const context = this;
      if (!inThrottle) {
        func.apply(context, args);
        inThrottle = true;
        setTimeout(() => inThrottle = false, limit);
      }
    };
  },

  formatDate(date, locale = 'zh-CN') {
    return new Intl.DateTimeFormat(locale, {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    }).format(new Date(date));
  },

  copyToClipboard(text) {
    if (navigator.clipboard) {
      return navigator.clipboard.writeText(text);
    } else {
      const textArea = document.createElement('textarea');
      textArea.value = text;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      return Promise.resolve();
    }
  }
};

